.mat-form-field+.mat-form-field {
  margin-left: 8px;
}

.mat-expansion-panel {
  margin-bottom: 10px;
  box-shadow: 0px 0px 7px -1px rgba(0, 0, 0, 0.08), 0 1px 1px 0 rgba(0, 0, 0, 0),
    0 1px 3px 0 rgba(0, 0, 0, 0) !important;
  border-radius: 10px !important;
}

label {
  display: inline-block;
  margin-bottom: 0;
  color: #3e3e3e;
  font-size: 13px !important;
  font-family: "GothamNarrowBold", sans-serif !important;
  min-width: 164px;
}

#status-toggle .btn-toggle:before {
  content: "off" !important;
  left: -3.5rem !important;
}

#status-toggle .btn-toggle:after {
  content: "On" !important;
  right: -3.5rem !important;
}

#status-toggle .btn-toggle {
  margin: 0 1.5rem !important;
}

textarea {
  resize: none !important;
  height: 90px;
}

.mat-form-field {
  width: 92% !important;
}

.alert-dark {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 115px;
  margin-top: 16px;
  color: #818181;
  background-color: #f2f2f5;
  border-color: #c6c8ca;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 17px;
  border-radius: 8px;
}

.alert-dark img {
  cursor: pointer;
}

.alert-dismissible {
  padding-right: 10px;
}

#profile .mat-expansion-panel-body .view-mode .mat-form-field-appearance-outline .mat-form-field-outline-end,
#profile .mat-expansion-panel-body .view-mode .mat-form-field-appearance-outline .mat-form-field-outline-start {
  border: none !important;
  min-width: 5px;
  background: #fff !important;
}

.email-block {
  z-index: 999999;
}

.c-input {
  color: #0072ce;
  font-family: "gothambook", sans-serif;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 16px;
}

.s-label {
  min-width: 104px !important;
}